import { TFindIdx, TUpdArray } from "../Pages/Tracks/Types";

export const findIdx: TFindIdx = (array, id) => {
  const idx = array.indexOf(array.find((elem) => `${elem.id}` === id)!);
  return idx;
}

export const updArray: TUpdArray = (array, idx, insElem, bool = false) => {
  const ans = insElem ?
    bool ? [...array.slice(0, idx), insElem, ...array.slice(idx)] : [...array.slice(0, idx), insElem, ...array.slice(idx + 1)] :
    [...array.slice(0, idx), ...array.slice(idx + 1)];
  return ans;
}