import React, { useCallback, useEffect } from "react";
import Track from "../../Components/Track/Track";
import { TTrack } from "../../types";
import "./Tracks.css";
import { TTrackMouseWrp } from "./Types";
import { DragDropContext, Droppable } from "react-beautiful-dnd";
import { useDispatch } from "react-redux";
import { useSelector } from "react-redux";
import { createTrackAC, readTracksAC, reorderTracksAC, setTrackNameAC } from "../../Store/Tracks/actionCreators";
import { Navigate } from "react-router-dom";
import { reorderTasksAC } from "../../Store/Tasks/actionCreators";

const Tracks = () => {
  const isFirstLoad = useSelector((state: any) => state.isFirstLoad);
  const shouldRedirect = useSelector((state: any) => state.shouldRedirect);
  const dispatch: any = useDispatch();
  const tracksState = useSelector((state: any) => state.tracks);
  const trackNameState = useSelector((state: any) => state.trackName);

  const reorderTasksWrp: TTrackMouseWrp = (srcTrackId, trackId, taskId, destIdx, sIdx) => {
    dispatch(reorderTasksAC(srcTrackId, trackId, taskId, destIdx));
  };

  const reorderTracksWrp = (id: string, idx: number, sIdx: number) => {
    dispatch(reorderTracksAC(id, idx, sIdx))
  };

  const tracks = tracksState.map((track: TTrack, idx: number) => (
    <Track
      trackIdx={idx}
      track={track}
      key={track.id}
      trackId={track.id}
    />));

  const onBeforeCapture = useCallback(() => {
    /*...*/
  }, []);
  const onBeforeDragStart = useCallback(() => {
    /*...*/
  }, []);
  const onDragStart = useCallback(() => {
    /*...*/
  }, []);
  const onDragUpdate = useCallback(() => {
    /*...*/
  }, []);
  const onDragEnd = (result: any) => {
    const { destination, source, draggableId } = result;
    if (destination) {
      if (source.droppableId === 'tracks-wrp') {
        reorderTracksWrp(draggableId, destination.index, source.index);
      } else {
        reorderTasksWrp(source.droppableId, destination.droppableId, draggableId, destination.index, source.index);
      }
    } else {
      if (source.droppableId === 'tracks-wrp') {

      } else {
        // removeTaskWrp(source.droppableId, draggableId);
      }
    }

  };

  useEffect(()=> {
    dispatch(readTracksAC());
  }, [])

  return (
    isFirstLoad ? <div>LOAD</div> :
    shouldRedirect ? <Navigate replace to="/login" /> :
    <DragDropContext
      onBeforeCapture={onBeforeCapture}
      onBeforeDragStart={onBeforeDragStart}
      onDragStart={onDragStart}
      onDragUpdate={onDragUpdate}
      onDragEnd={onDragEnd}
    >
      <div className="tracks-wrp">
        <div className="tracks__control">
          <input className="tracks__input" onChange={(e) => { dispatch(setTrackNameAC(e.target.value)) }} value={trackNameState} />
          <button className="tracks__btn" onClick={() => { dispatch(createTrackAC(trackNameState, tracks.length)) }}>Add track</button>
        </div>
        <Droppable droppableId="tracks-wrp" type="TRACKS" direction="horizontal">
          {(provided, snapshot) => (
            <div
              ref={provided.innerRef}
              // style={{ backgroundColor: snapshot.isDraggingOver ? 'blue' : 'grey' }}
              {...provided.droppableProps}
              className="tracks__items">{tracks}
              {provided.placeholder}
            </div>
          )}
        </Droppable>
      </div>
    </DragDropContext>
  )
};

export default Tracks;