import React, { useState } from "react";

const NewTask = (props: {onSave: any, onClose: any}) => {

  const [taskName, setTaskName] = useState('');
  const [taskDesc, setTaskDesc] = useState('');

  return (
    <div className="new-task-modal">
      <input onChange={(e) => setTaskName(e.target.value)} type="text" value={taskName} />
      <input onChange={(e) => setTaskDesc(e.target.value)} type="text" value={taskDesc} />
      <button onClick={() => props.onSave(taskName, taskDesc)}>Save task</button>
      <button onClick={props.onClose}>Close</button>
    </div>
  );
};

export default NewTask;