import { useState } from "react";
import { Link } from "react-router-dom";
import { loginAC } from "../../Store/actionCreators";
import { useDispatch } from "react-redux";

export const Login = () => {
  const [editName, setEditName] = useState('');
  const [editPass, setEditPass] = useState('');

  const dispatch: any = useDispatch()

  const clickHandler = (e: React.MouseEvent<HTMLButtonElement, MouseEvent>) => {
    e.preventDefault();
    dispatch(loginAC(editName, editPass));
  }

  return (
    <div>
      Login page
      <form action="">
        <label htmlFor="">Логин:</label>
        <input onChange={(e) => { setEditName(e.target.value) }} value={editName} type="text" />
        <label htmlFor="">Пароль:</label>
        <input onChange={(e) => { setEditPass(e.target.value) }} value={editPass} type="text" />
        <button onClick={clickHandler}>Войти</button>
        <hr />
        <div>Нет аккаунта? <Link to="/register">Зарегистрироваться</Link></div>
      </form>
    </div>
  );
}