import React, { useState } from "react";
import ReactDOM from "react-dom";
import NewTask from "../NewTask";
import Task from "../Task/Task";
import { TTask } from "../../types";
import { TrackElement} from "./Type";
import { cancelName, saveName, toggleEdit, toggleModal, addTask } from "./Service";
import { Draggable, Droppable } from "react-beautiful-dnd";
import DeleteIcon from '@mui/icons-material/Delete';
import EditIcon from '@mui/icons-material/Edit';
import DoneIcon from '@mui/icons-material/Done';
import ClearIcon from '@mui/icons-material/Clear';
import { useDispatch } from "react-redux";
import { TAddTaskWrp, TEditTrackWrp, TRemoveTrackWrp } from "../../Pages/Tracks/Types";
import { createTaskAC } from "../../Store/Tasks/actionCreators";
import { removeTrackAC, renameTrackAC } from "../../Store/Tracks/actionCreators";

const Track: TrackElement = ({ track, trackId, trackIdx }) => {

  const dispatch: any = useDispatch();

  const tasksArr = track.tasks;
  const [isModalOpen, setModalOpen] = useState<boolean>(false);
  const [isEditMode, setEditMode] = useState<boolean>(false);
  const [editNameValue, setEditNameValue] = useState<string>(track.name);

  const addTaskWrp: TAddTaskWrp = (trackId, name, desc) => {
    dispatch(createTaskAC(trackId, name, desc));
  };

  const removeTrackWrp: TRemoveTrackWrp = (trackId, trackIdx) => {
    dispatch(removeTrackAC(trackId, trackIdx));
  };

  const editTrackWrp: TEditTrackWrp = (trackId, newName) => {
    dispatch(renameTrackAC(trackId, newName));
  };

  const toggleModalWrp = () => { toggleModal(setModalOpen, isModalOpen) }
  const toggleEditWrp = () => { toggleEdit(setEditMode, isEditMode) }
  const cancelNameWrp = () => { cancelName(setEditNameValue, track.name, setEditMode, isEditMode) }
  const saveNameWrp = () => { saveName(editTrackWrp, trackId, editNameValue, setEditMode, isEditMode) }
  const addTaskWrapper = (name: string, desc: string) => { addTask(addTaskWrp, trackId, name, desc, setModalOpen, isModalOpen) }

  // const tasks = tasksArr.map((task: TTask, idx: number) => (
  //   <Task
  //     taskIdx={idx}
  //     trackId={trackId}
  //     task={task}
  //     key={task.id}
  //     taskId={task.id}
  //   />));

  return (
    <Draggable draggableId={`${trackId}`} index={trackIdx}>
      {(providedDrag, snapshotDrag) => (
        <Droppable droppableId={`${track.id}`}>
          {(provided, snapshot) => (
            <div
              className="track"
              ref={providedDrag.innerRef}
              {...providedDrag.draggableProps}
              {...providedDrag.dragHandleProps}
            >
              <div className="track__control">
                {!isEditMode ?
                  <>
                    <h3 className="track-name">{track.name}</h3>
                    <div className="track__btns">
                      <EditIcon onClick={toggleEditWrp}></EditIcon>
                      <DeleteIcon onClick={() => { removeTrackWrp(trackId, trackIdx) }}></DeleteIcon>
                    </div>
                  </> :
                  <>
                    <input className="track-name" onChange={(e) => { setEditNameValue(e.target.value) }} value={editNameValue} type="text" />
                    <div className="track__btns">
                      <DoneIcon onClick={saveNameWrp}></DoneIcon>
                      <ClearIcon onClick={cancelNameWrp}></ClearIcon>
                    </div>
                  </>
                }
              </div>

              <div
                className="track-tasks"
                ref={provided.innerRef}
                {...provided.droppableProps}
              >{/*tasks*/}{provided.placeholder}</div>

              <button onClick={toggleModalWrp} className="add-task-btn">Add task</button>
              {isModalOpen && ReactDOM.createPortal(
                <NewTask onSave={(name: string, desc: string) => { addTaskWrapper(name, desc) }} onClose={toggleModal} />,
                document.getElementById('portal')!
              )}
            </div>
          )}
        </Droppable>
      )}
    </Draggable>
  )
};

export default Track;