import React from "react";
import { useSelector } from "react-redux";
import { Navigate } from 'react-router-dom';
const Main = () => {
  const isFirstLoad = useSelector((state: any) => state.isFirstLoad);
  const shouldRedirect = useSelector((state: any) => state.shouldRedirect);

  return (
    isFirstLoad ? <div>LOAD</div> :
      shouldRedirect ? <Navigate replace to="/login" /> :
        <>
          Main text
        </>
  )
};

export default Main;