import React, { useEffect } from 'react';
import './App.css';
import { Routes, Route, useNavigate } from "react-router-dom";
import Header from './Components/Header';
import Main from './Pages/Main/Main';
import Tracks from './Pages/Tracks/Tracks';
import { Login } from './Pages/Login/Login';
import { Register } from './Pages/Register/Register';
import { useSelector } from 'react-redux';
import { setIsFirstLoadAC } from './Store/actionCreators';
import { useDispatch } from 'react-redux';

function App() {
  const isFirstLoad = useSelector((state: any) => state.isFirstLoad);
  const dispatch: any = useDispatch();
  const shouldRedirect = useSelector((state: any) => state.shouldRedirect);
  const navigate = useNavigate();
  useEffect(() => {
    if (isFirstLoad) {
      dispatch(setIsFirstLoadAC(false))
    } else if (!shouldRedirect) {
      navigate('/');
    } else {
      navigate('/login');
    }
  }, [shouldRedirect])
  return (
    <div className="container">
      {shouldRedirect || <Header />}
      <Routes>
        <Route path='/' element={<Main />} />
        <Route path='/tracks' element={<Tracks />} />
        <Route path='/login' element={<Login />} />
        <Route path='/register' element={<Register />} />
        <Route path="*" element={404} />
      </Routes>
    </div>
  );
}

export default App;
