import { useState } from "react";
import { Link } from "react-router-dom";
import { useDispatch } from "react-redux";
import { registerAC } from "../../Store/actionCreators";
import { MinMax } from "../../Components/MInMax";

export const Register = () => {
  const [editName, setEditName] = useState('');
  const [editPass, setEditPass] = useState('');
  const [editAge, setEditAge] = useState('');
  const dispatch: any = useDispatch();

  const clickHandler = (e: React.MouseEvent<HTMLButtonElement, MouseEvent>) => {
    e.preventDefault();
    if (editAge) {
      dispatch(registerAC(editName, editPass, +editAge));
    }
  }

  return (
    <div>
      Registration page
      <form action="">
        <label htmlFor="">Логин:</label>
        <input onChange={(e) => { setEditName(e.target.value) }} value={editName} type="text" />
        <label htmlFor="">Пароль:</label>
        <input onChange={(e) => { setEditPass(e.target.value) }} value={editPass} type="text" />
        <label htmlFor="">Возраст:</label>
        <MinMax min={12} max={99} current={editAge} onChange={setEditAge} />
        <button onClick={clickHandler}>Зарегистрироваться</button>
        <hr />
        <div>Уже есть аккаунт? <Link to="/login">Войти</Link></div>
      </form>
    </div>
  );
}