import axios from "axios";
import { TOGGLE_REDIRECT, TOGGLE_FIRST, SET_INITIAL_STATE } from "./actions";
import baseURL from "./baseUrl";

axios.defaults.withCredentials = true;
const URL = `${baseURL}/auth/`;

export const setShouldRedirectAC = (should: boolean) => ({ type: TOGGLE_REDIRECT, should });

export const setInitialState = () => ({ type: SET_INITIAL_STATE });

export const setIsFirstLoadAC = (first: boolean) => {
  return (dispatch: any, getState: any) => {
    if (getState().shouldRedirect) {
      axios.get(URL + 'check', { withCredentials: true }).then(
        (res) => {
          if (res.status === 200) {
            dispatch(setShouldRedirectAC(first));
          }
        }
      ).catch()
        .finally(() => { dispatch(({ type: TOGGLE_FIRST, first })); })
    }
  }
};

export const loginAC = (name: string, pass: string) => {
  return (dispatch: any) => {
    axios.post(URL + 'login', {
      username: name,
      password: pass,
    }).then((res) => {
      if (res.status === 200) {
        dispatch(setShouldRedirectAC(false));
      }
    });
  }
}

export const logoutAC = () => {
  return (dispatch: any) => {
    axios.get(URL + 'logout').then(
      (res) => {
        dispatch(setShouldRedirectAC(true));
        // dispatch(setInitialState());
      }
    );
  }
}

export const registerAC = (name: string, pass: string, age: number) => {
  return (dispatch: any) => {
    axios.post(URL + 'registration', {
      username: name,
      password: pass,
      age: age,
    }).then((res) => {
      if (res.status === 200) {
        dispatch(setShouldRedirectAC(false));
      }
    });
  }
}