import { REORDER_TRACKS, REMOVE_TRACK, CREATE_TRACK, UPDATE_TRACK, SET_TRACK_NAME, SET_TRACKS } from './Tracks/actions';
import { REORDER_TASKS, REMOVE_TASK, CREATE_TASK, UPDATE_TASK } from './Tasks/actions';
import { TOGGLE_REDIRECT, TOGGLE_FIRST, SET_INITIAL_STATE } from './actions';
import { reorderTasks, removeTask, addTask, editTask } from './Tasks/actionCreators';
import { reorderTracks } from './Tracks/actionCreators';
import { initialState } from './initialState';

export const reducer = (state = initialState, action: any) => {
  switch (action.type) {
    case REORDER_TRACKS: return { ...state, tracks: reorderTracks(action.id, action.idx, state.tracks) }
    case REMOVE_TRACK: return { ...state, tracks: state.tracks.filter((track) => track.id !== action.id) }
    case CREATE_TRACK: return { ...state, tracks: [...state.tracks, { ...action.track }] }
    case UPDATE_TRACK: return { ...state, tracks: state.tracks.map((track) => track.id === action.id ? { ...track, name: action.name } : track) }

    case SET_TRACK_NAME: return { ...state, trackName: action.trackName }
    case SET_TRACKS: return { ...state, tracks: action.tracks }

    case REORDER_TASKS: return { ...state, tracks: reorderTasks(action.srcTrackId, action.trackId, state.tracks, action.taskId, action.destIdx) }
    case REMOVE_TASK: return { ...state, tracks: removeTask(action.trackId, action.taskId, state.tracks) }
    case CREATE_TASK: return { ...state, tracks: addTask(action.trackId, action.name, action.desc, state.tracks, action.taskId) }
    case UPDATE_TASK: return { ...state, tracks: editTask(action.trackId, action.taskId, action.newName, action.newDesc, state.tracks) }

    case TOGGLE_REDIRECT: return { ...state, shouldRedirect: action.should }
    case TOGGLE_FIRST: return { ...state, isFirstLoad: action.first }

    case SET_INITIAL_STATE: return { ...initialState }

    default: return state;
  }
}