import axios from 'axios';
import { TTracks } from '../../types';
import { REORDER_TRACKS, REMOVE_TRACK, CREATE_TRACK, SET_TRACKS, UPDATE_TRACK, SET_TRACK_NAME } from './actions';
import { findIdx, updArray } from '../utils';
import baseURL from "./../baseUrl";
axios.defaults.withCredentials = true;
const URL = `${baseURL}/tracks/`;

export const createTrackAC = (name: string, order: number) => {
  return (dispatch: any) => {
    axios.post(URL, {
      name,
      tasks: [],
      order
    }, { withCredentials: true })
      .then((res) => {
        dispatch(({ type: CREATE_TRACK, track: { name, tasks: [], id: res.data.id } }))
      })
      .catch();
  }
}

export const readTracksAC = () => {
  return (dispatch: any) => {
    axios.get(URL, { withCredentials: true }).then(
      (res) => {
        const tracks = res.data.map((track: any) => (track)).sort(
          (a: any, b: any) => a.order > b.order ? 1 : -1
        );
        console.log(tracks)
        dispatch(setTracksAC(tracks));
      }
    ).catch(
      (res) => {
        dispatch(setTracksAC([]))
      }
    )
  }
};

export const renameTrackAC = (id: string, name: string) => {
  return (dispatch: any) => {
    axios.put(URL + id, { name }, { withCredentials: true }).then(
      (res) => { dispatch(({ type: UPDATE_TRACK, id, name })); }
    );
  }
};

export const removeTrackAC = (id: string, idx: number) => {
  return (dispatch: any) => {
    axios.delete(URL + id+'_'+idx, { withCredentials: true }).then(
      (res) => {
        dispatch(({ type: REMOVE_TRACK, id }));
      }
    )
  }
};

export const reorderTracksAC = (id: string, idx: number, sIdx: number) => {
  return (dispatch: any) => {
    axios.put(URL, { id, idx, sIdx }, { withCredentials: true }).then(
      (res) => { dispatch({ type: REORDER_TRACKS, id, idx }) }
    )
  }
}

export const setTrackNameAC = (trackName: string) => ({ type: SET_TRACK_NAME, trackName });

export const reorderTracks = (draggableId: string, index: number, tracksState: TTracks) => {
  const srcTrackIdx = findIdx(tracksState, draggableId);
  const targetTrack = { ...tracksState[srcTrackIdx] };
  const newSrcTracksList = updArray(tracksState, srcTrackIdx);
  const editedTrackList = updArray(newSrcTracksList, index, targetTrack, true);
  return editedTrackList;
}

const setTracksAC = (tracks: TTracks) => ({ type: SET_TRACKS, tracks })