import { TAddTaskWrp, TEditTrackWrp } from "../../Pages/Tracks/Types";
import { TAction } from "../../types";

type TToggleModal = (setModalOpen: TAction<boolean>, isModalOpen: boolean) => void;
export const toggleModal: TToggleModal = (setModalOpen, isModalOpen) => { setModalOpen(!isModalOpen); };

type TToggleEdit = (setEditMode: TAction<boolean>, isEditMode: boolean) => void;
export const toggleEdit: TToggleEdit = (setEditMode, isEditMode) => { setEditMode(!isEditMode); };

type TCancelName = (setEditNameValue: TAction<string>, trackName: string, setEditMode: TAction<boolean>, isEditMode: boolean) => void;
export const cancelName: TCancelName = (setEditNameValue, trackName, setEditMode, isEditMode) => { setEditNameValue(trackName); toggleEdit(setEditMode, isEditMode); };

type TSaveName = (editTrackWrp: TEditTrackWrp, trackId: string, editNameValue: string, setEditMode: TAction<boolean>, isEditMode: boolean) => void;
export const saveName: TSaveName = (editTrackWrp, trackId, editNameValue, setEditMode, isEditMode) => { editTrackWrp(trackId, editNameValue); toggleEdit(setEditMode, isEditMode); };

type TAddTask = (addTaskWrp: TAddTaskWrp, trackId: string, name: string, desc: string, setModalOpen: TAction<boolean>, isModalOpen: boolean) => void;
export const addTask: TAddTask = (addTaskWrp, trackId, name, desc, setModalOpen, isModalOpen) => { addTaskWrp(trackId, name, desc); toggleModal(setModalOpen, isModalOpen); };