import { TEditTask, TAddTask, TRemoveTask, TSetTasks, TTrackMouse } from "../../Pages/Tracks/Types";
import { REORDER_TASKS, REMOVE_TASK, CREATE_TASK, UPDATE_TASK } from "./actions";
import { findIdx, updArray } from "../utils";
import { v4 as uuidv4 } from 'uuid';
import axios from "axios";
import baseURL from "./../baseUrl";
axios.defaults.withCredentials = true;
const URL = `${baseURL}/tasks/`;

export const reorderTasksAC = (srcTrackId: string, trackId: string, taskId: string, destIdx: number) => ({ type: REORDER_TASKS, srcTrackId, trackId, taskId, destIdx });

export const removeTaskAC = (trackId: string, taskId: string) => {
  return (dispatch: any) => {
    axios.delete(URL + trackId + '/' + taskId, { withCredentials: true }).then(
      (res) => { dispatch(({ type: REMOVE_TASK, trackId, taskId })); }
    )
  }
}

export const createTaskAC = (trackId: string, name: string, desc: string) => {
  return (dispatch: any) => {
    const id = uuidv4();
    axios.post(URL + trackId, {
      id,
      name,
      desc
    }, { withCredentials: true }).then(
      (res) => { dispatch(({ type: CREATE_TASK, trackId, name, desc, taskId: id })); }
    )
  }
};

export const updateTaskAC = (trackId: string, taskId: string, newName: string, newDesc: string) => {
  return (dispatch: any) => {
    axios.put(URL + trackId + '/' + taskId,
      {
        name: newName,
        desc: newDesc,
      }, { withCredentials: true }).then(
        (res) => { dispatch(({ type: UPDATE_TASK, trackId, taskId, newName, newDesc })) }
      )
  }
};

export const editTask: (TEditTask) = (trackId, taskId, newName, newDesc, tracksState) => {
  const trackIdx = findIdx(tracksState, trackId);
  const targetTasksList = tracksState[trackIdx].tasks;
  const taskIdx = findIdx(targetTasksList, taskId);
  const editedTask = { ...targetTasksList[taskIdx], name: newName, desc: newDesc };
  const newTasksList = updArray(targetTasksList, taskIdx, editedTask);
  const editedTrack = { ...tracksState[trackIdx], tasks: newTasksList };
  const newTracksList = updArray(tracksState, trackIdx, editedTrack);
  return newTracksList;
};
export const addTask: (TAddTask) = (trackId, name, desc, tracksState, taskId) => {
  const trackIdx = findIdx(tracksState, trackId);
  const targetTasksList = tracksState[trackIdx].tasks;
  const newTasksList = [...targetTasksList];
  newTasksList.push({ name: name, desc: desc, id: taskId });
  const editedTrack = { ...tracksState[trackIdx], tasks: newTasksList };
  const newTracksList = updArray(tracksState, trackIdx, editedTrack);
  return newTracksList;
};
export const removeTask: (TRemoveTask) = (trackId, taskId, tracksState) => {
  const trackIdx = findIdx(tracksState, trackId);
  const targetTasksList = tracksState[trackIdx].tasks;
  const taskIdx = findIdx(targetTasksList, taskId);
  const newTasksList = updArray(targetTasksList, taskIdx);
  const editedTrack = { ...tracksState[trackIdx], tasks: newTasksList };
  const newTracksList = updArray(tracksState, trackIdx, editedTrack);
  return newTracksList;
};
export const setTasks: (TSetTasks) = (trackId, updTasks, tracksState) => {
  const trackIdx = findIdx(tracksState, trackId);
  const editedTrack = { ...tracksState[trackIdx], tasks: updTasks };
  const newTrackState = updArray(tracksState, trackIdx, editedTrack);
  return newTrackState;
}
export const reorderTasks: (TTrackMouse) = (srcTrackId, trackId, tracksState, taskId, destIdx) => {
  const srcTrackIdx = findIdx(tracksState, srcTrackId);
  const srcTasksList = tracksState[srcTrackIdx].tasks;
  const srcTaskIdx = findIdx(srcTasksList, taskId);
  const targetTask = { ...srcTasksList[srcTaskIdx] };
  const newSrcTasksList = updArray(srcTasksList, srcTaskIdx);
  if (srcTrackId === trackId) {
    const newTargetTasksList = updArray(newSrcTasksList, destIdx, targetTask, true);
    const editedTrack = { ...tracksState[srcTrackIdx], tasks: newTargetTasksList };
    const editedTrackList = updArray(tracksState, srcTrackIdx, editedTrack);
    return editedTrackList;
  } else {
    const editedSrcTrack = { ...tracksState[srcTrackIdx], tasks: newSrcTasksList };
    const srcEditedTrackList = updArray(tracksState, srcTrackIdx, editedSrcTrack);
    const trackIdx = findIdx(tracksState, trackId);
    const destTasksList = tracksState[trackIdx].tasks;
    const newTargetTasksList = updArray(destTasksList, destIdx, targetTask, true);
    const editedTrack = { ...tracksState[trackIdx], tasks: newTargetTasksList };
    const editedTrackList = updArray(srcEditedTrackList, trackIdx, editedTrack);
    return editedTrackList;
  }
};