import { useEffect, useRef, useState } from "react";

type TMinMax = (props: { min: number, max: number, current: string, onChange: any }) => JSX.Element;

export const MinMax: TMinMax = ({ min, max, current, onChange }) => {
  const [editInpVal, setEditInpVal] = useState(current);
  const ref: React.LegacyRef<HTMLInputElement> = useRef(null);

  const checkValue = () => {
    const value = parseInt(ref.current!.value);
    const newVal = isNaN(value) ? min : Math.max(min, Math.min(max, value));
    ref.current!.value = `${newVal}`;
    onChange(newVal);
  }

  useEffect(() => {
    ref.current!.value = `${current}`;
    setEditInpVal(current);
  }, [current])

  return (
    <input ref={ref} defaultValue={editInpVal} type="text" onBlur={checkValue} />
  )
}